import React, { Component } from 'react';
import {browserHistory} from "react-router";

import {  withRouter, Redirect } from 'react-router-dom';
import { connect } from 'react-redux';
import { activateNonAuthLayout } from '../../../store/actions';
import { reactLocalStorage } from 'reactjs-localstorage';

class Logout extends Component {

    constructor(props) {
        super(props);
        this.state = {  };
    }

    componentDidMount() {
        // this.props.activateNonAuthLayout();
        // Remove all Item from localstorage and redirect to login page
        // localStorage.removeItem('user');
        reactLocalStorage.remove('login_data');
        reactLocalStorage.remove('loginStatus');
        reactLocalStorage.remove('login');
        window.location.href = "/login";
    }

    render() {
        return (
            <React.Fragment>
                <Redirect to='/login'/>
            </React.Fragment>
        );
    }
}

const mapStatetoProps = state => {
     return {  };
}

export default withRouter(connect(mapStatetoProps,{ activateNonAuthLayout })(Logout));

