import React, { Component } from "react";
import { Row, Col, Card, CardBody, Button, Alert } from "reactstrap";
// import { Row, Col, Card, CardBody, Alert } from 'reactstrap';
import { activateAuthLayout } from '../../../store/actions';
import { connect } from "react-redux";
import { AvForm, AvField } from "availity-reactstrap-validation";
import Settingmenu from "../Subpages/Settingmenu";

import Toggle from "react-toggle";
import "react-toggle/style.css";

import { Link } from "react-router-dom";
import HttpClient from "../../../utils/HttpClient";

class FormElements extends Component {
  constructor(props) {
    super(props);
    this.state = {
      code: "",
      campaign_for:"",
      alert: false,
      error: false,
      error2:false,
      msg:'',
      carmodel:""
    };
    this.add_bClass = this.add_bClass.bind(this);
    this.add_vClass = this.add_vClass.bind(this);
  }

  componentDidMount() {
    this.props.activateAuthLayout();
}

  add_bClass = () => {
    document
      .getElementById("bootstrap_validation")
      .classList.add("was-validated");
  };
  add_vClass = () => {
    document.getElementById("b_validation").classList.add("was-validated");
  };


  async handleSubmit() {

    // if (this.state.code.length!=6) {
    //   this.setState({ error2: true });
    //   setTimeout(async () => {
    //     this.setState({ error2: false });
    //   }, 5000);
    //   return false;
    // }
    
    const data = {
      carmodel: this.state.carmodel,
    };

    console.log("data", data);
    if (this.state.carmodel !== "") {
      let dataStatus = await HttpClient.requestData("carmodel", "POST", data);
      console.log("dataStatus", dataStatus);
      if (dataStatus.status) {
        this.setState({ alert: true, carmodel: ""});
        setTimeout(async () => {
          this.setState({ alert: false });
        }, 5000);
      } else {

        this.setState({ error: true,msg:dataStatus.error[0].msg });
        setTimeout(async () => {
          this.setState({ error: false });
        }, 5000);
      }
    } else {
      this.setState({ error: true });
      setTimeout(async () => {
        this.setState({ error: false });
      }, 5000);
    }
  }

  render() {
    return (
      <React.Fragment>
        <div className="content">
          <div className="container-fluid">
            <div className="page-title-box">
              <Row className="align-items-center">
                <Col sm="6">
                  <h4 className="page-title">Add Car Model</h4>
                  <ol className="breadcrumb">
                    <li className="breadcrumb-item">
                      <Link to="/home">
                        <i className="mdi mdi-home-outline"></i>
                      </Link>
                    </li>
                    <li className="breadcrumb-item">
                      <Link to="#">Car Model</Link>
                    </li>
                    <li className="breadcrumb-item active">Add Car Model</li>
                  </ol>
                </Col>
                <Col sm="6">
                  <div className="float-right d-none d-md-block">
                    <Settingmenu />
                  </div>
                </Col>
              </Row>
            </div>

            <Row>
              <Col>
                <Card>
                  <CardBody>
                    <h4 className="mt-0 header-title">Add Car Model</h4>
                    {this.state.alert ? (
                      <Alert
                        color="success"
                        isOpen={this.state.alert}
                        toggle={() => this.setState({ alert: false })}
                      >
                        <i className="mdi mdi-check-all mr-2"></i> Success !!!
                        Car Model Added Successfully
                      </Alert>
                    ) : null}
                    {this.state.error ? (
                      <Alert
                        color="danger"
                        isOpen={this.state.error}
                        toggle={() => this.setState({ error: false })}
                      >
                        <i className="mdi mdi-block-helper mr-2"></i> Sorry !!! 
                        {this.state.msg}
                      </Alert>
                    ) : null}
                    {/* {this.state.error2 ? (
                      <Alert
                        color="danger"
                        isOpen={this.state.error2}
                        toggle={() => this.setState({ error2: false })}
                      >
                        <i className="mdi mdi-block-helper mr-2"></i> Enter Code Of Length 6
                      </Alert>
                    ) : null} */}
                    <AvForm>
                      <AvField
                        name="carmodel"
                        label="Car Model : "
                        placeholder="Car Model"
                        type="text"
                        errorMessage="Enter Car Model"
                        value={this.state.carmodel}
                        validate={{ required: { value: true } }}
                        onChange={val =>
                          this.setState({
                            carmodel: val.target.value
                          })
                        }
                      />

                      {/* <AvField
                        name="campain_for"
                        label="Campaign For : "
                        placeholder="Campaign For"
                        type="text"
                        errorMessage="Enter Campaign For"
                        value={this.state.campaign_for}
                        validate={{ required: { value: true } }}
                        onChange={val =>
                          this.setState({
                            campaign_for: val.target.value
                          })
                        }
                      /> */}

                      <div className="form-group  m-b-0">
                        <Button
                          color="primary"
                          onClick={() => {
                            this.handleSubmit();
                            // onClick={this.add_bClass}
                          }}
                        >
                          Submit
                        </Button>{" "}
                        &nbsp;
                        <Button type="reset">cancel</Button>
                      </div>
                    </AvForm>
                  </CardBody>
                </Card>
              </Col>
            </Row>
          </div>
        </div>
      </React.Fragment>
    );
  }
}

export default connect(null,{ activateAuthLayout })(FormElements);
