import React, { Component } from "react";
import { Row, Col, Card, CardBody, Button } from "reactstrap";
import { MDBDataTable } from "mdbreact";
import { activateAuthLayout } from '../../../store/actions';
import { connect } from "react-redux";
import Settingmenu from "../Subpages/Settingmenu";
import { Link } from "react-router-dom";
import Toast, { types, positions, animations } from "react-alert-toast";
import HttpClient from "../../../utils/HttpClient";

class Tabledatatable extends Component {
  constructor(props) {
    super(props);
    this.state = {
      data: [],
      result: []
    };
  }

  componentDidMount() {
    this.props.activateAuthLayout();
    // this.Fetch();
    this.FetchAll();
  }

  async status(index,data, state, e) {
    console.log("result", index);
    let result = await HttpClient.requestData("dialogically/suspended", "PUT", data);
    console.log("result", result);
    if (result && result.status === true) {
      // this.FetchAll();
      let data = result.data;
      this.state.result[index] = data;
      this.setState({});
      this.UpdateState();
      // console.log("mask profileeeeee", this.state.result);
      // this.setState({
      //   result: result
      // });
    }
  }


  async UpdateState()
  {
    let data = [];
    let i = 1;
    this.state.result.forEach((element,index) => {
      var length = element.content.length;
      var content=null;
      if (length<=80) {
         content = element.content.substring(0, 35);
      }
      else{
         content = element.content.substring(0, 35) + " ....";
      }
      
      let rows = {
        sl: i++,
        content: content,
        media: element.media.length>0?<img src={element.media[0].url} alt="" className="rounded thumb-lg" />:"No Media",
        square:element.square_data.length>0 ? element.square_data[0].squ_name:null,
        user: <div className="media">
        <img className="mr-3 thumb-md rounded-circle" src={element.user_data[0].image} alt="" />
        <div className="media-body" style={{alignSelf:'center'}}>
            <h6 className="mt-0"> {element.user_data[0].firstname+" "+element.user_data[0].lastname}</h6>
        </div>
    </div>,
        status:
          element.is_deleted !== true ? (
            <Button
              className="btn-icon"
              color="success"
              onClick={e => this.status(index,element, element.is_deleted, e)}
            >
              {" "}
              <span className="btn-icon-label">
                <i className="mdi mdi-emoticon-wink-outline mr-2"></i>
              </span>{" "}
              Active
            </Button>
          ) : (
            <Button
              className="btn-icon"
              color="danger"
              onClick={e => this.status(index,element, element.is_deleted, e)}
            >
              {" "}
              <span className="btn-icon-label">
                <i className="mdi mdi-emoticon-cry-outline mr-2"></i>
              </span>{" "}
              Deactive
            </Button>
          ),
        view: (
          <Button className="btn-icon" color="primary">
            {" "}
            <span className="btn-icon-label">
              <i className="mdi mdi-account-circle-outline mr-2"></i>
            </span>
            View
          </Button>
        ),
       
      };
      data.push(rows);
      this.setState({
        data: data
      });
     
   
  });
  }

  async FetchAll() {
    let result = await HttpClient.requestData("dialogically/report", "GET");
    console.log("result", result);

    if (result && result.length > 0) {
      let data = [];
      let i = 1;
      // let index =0;
      this.setState({result:result});

      this.state.result.forEach((element,index) => {

        var length = 0 ;
        if (element.dialogically_data[0]) {
          length = element.dialogically_data[0].content.length;
        }
           
          var content=null;
          if (length<=80) {
             content = element.dialogically_data[0].content.substring(0, 35);
          }
          else{
             content = element.dialogically_data[0].content.substring(0, 35) + " ....";
          }
          
          let rows = {
            sl: i++,
            content: content,
            media: element.dialogically_data[0].media.length>0?<img src={element.dialogically_data[0].media[0].url} alt="" className="rounded thumb-lg" />:"No Media",
            reason:element.reason,
            user: <div className="media">
            <img className="mr-3 thumb-md rounded-circle" src={element.dialogically_data[0].UserData.image} alt="" />
            <div className="media-body" style={{alignSelf:'center'}}>
                <h6 className="mt-0"> {element.dialogically_data[0].UserData.firstname+" "+element.dialogically_data[0].UserData.lastname}</h6>
            </div>
            </div>,
            reportuser: <div className="media">
            <img className="mr-3 thumb-md rounded-circle" src={element.report_by[0].image} alt="" />
            <div className="media-body" style={{alignSelf:'center'}}>
                <h6 className="mt-0"> {element.report_by[0].firstname+" "+element.report_by[0].lastname}</h6>
            </div>
            </div>,
            view: (
              <Button className="btn-icon" color="primary">
                {" "}
                <span className="btn-icon-label">
                  <i className="mdi mdi-account-circle-outline mr-2"></i>
                </span>
                View
              </Button>
            ),

          };
          data.push(rows);
         
         
       
      });
      console.log("this.state.data", data);
          this.setState({
            data: data
          });
    } else{
        // let data = [];
        this.setState({
            data: []
          });
    }
  }

  render() {
    const data = {
      columns: [
        {
          label: "Sl.",
          field: "sl",
          sort: "asc",
          width: 150
        },
        {
          label: "Content",
          field: "content",
          sort: "asc",
          width: 150
        },
        {
          label: "Media",
          field: "media",
          sort: "asc",
          width: 150
        },
        // {
        //   label: "Square",
        //   field: "square",
        //   sort: "asc",
        //   width: 150
        // },
        {
          label: "Reason",
          field: "reason",
          sort: "asc",
          width: 270
        },
        {
          label: "User Details",
          field: "user",
          sort: "asc",
          width: 200
        },
        {
            label: 'Report User',
            field: 'reportuser',
            sort: 'asc',
            width: 100
        },
        // {
        //     label: 'Edit',
        //     field: 'edit',
        //     sort: 'asc',
        //     width: 150
        // },
        // {
        //     label: 'Delete',
        //     field: 'delete',
        //     sort: 'asc',
        //     width: 100
        // },
        // {
        //   label: "Status",
        //   field: "status",
        //   sort: "asc",
        //   width: 150
        // },
        {
          label: "View",
          field: "view",
          sort: "asc",
          width: 150
        },
        // {
        //   label: "View Mask",
        //   field: "mask",
        //   sort: "asc",
        //   width: 150
        // }
      ],
      rows: this.state.data
    };

    return (
      <React.Fragment>
        <div className="content">
          <div className="container-fluid">
            <div className="page-title-box">
              <Row className="align-items-center">
                <Col sm="6">
                  <h4 className="page-title">Dialogically</h4>
                  <ol className="breadcrumb">
                    <li className="breadcrumb-item">
                      <Link to="/home">
                        <i className="mdi mdi-home-outline"></i>
                      </Link>
                    </li>
                    <li className="breadcrumb-item">
                      <Link to="#">Report Dialogically</Link>
                    </li>
                    <li className="breadcrumb-item active">Report Dialogically</li>
                  </ol>
                </Col>
                <Col sm="6">
                  <div className="float-right d-none d-md-block">
                    {/* <Toast
                                        message="Succefully Saved To Playlist"
                                        type={types.SUCCESS}
                                        position={positions.BOTTOM_CENTER}
                                        // icon={success}
                                        animation={animations.FADE}
                                        delayBeforeLoad={1000}
                                        closeButton={true}
                                        style={{ border: "2px solid #fff", color: "#ffffff" }}
                                        /> */}
                  </div>
                </Col>
              </Row>
            </div>

            <Row>
              <Col>
                <Card>
                  <CardBody>
                    <h4 className="mt-0 header-title">Dialogically Report List</h4>
                    <MDBDataTable bordered data={data} style={{overflow:'wrap'}} />
                  </CardBody>
                </Card>
              </Col>
            </Row>
          </div>
        </div>
      </React.Fragment>
    );
  }
}

export default connect(null,{ activateAuthLayout })(Tabledatatable);
