import React, { Component } from "react";
import { Row, Col, Card, CardBody, Button, Alert } from "reactstrap";
// import { Row, Col, Card, CardBody, Alert } from 'reactstrap';
import { activateAuthLayout } from '../../../store/actions';
import { connect } from "react-redux";
import { AvForm, AvField } from "availity-reactstrap-validation";
import Settingmenu from "../Subpages/Settingmenu";
import Select from 'react-select';

import Toggle from "react-toggle";
import "react-toggle/style.css";

import { Link } from "react-router-dom";
import HttpClient from "../../../utils/HttpClient";




class RewardPoint extends Component {
    constructor(props) {
        super(props);
        this.state = {
            name: "",
            selectedGroup: null,
            domain: [],
            dom_id: '',
            alert: false,
            error: false,
            message: '',
            rewardPoint: {}

        };
        this.add_bClass = this.add_bClass.bind(this);
        this.add_vClass = this.add_vClass.bind(this);
    }
    add_bClass = () => {
        document
            .getElementById("bootstrap_validation")
            .classList.add("was-validated");
    };
    add_vClass = () => {
        document.getElementById("b_validation").classList.add("was-validated");
    };
    componentDidMount() {
        this.props.activateAuthLayout();
        this.rewardPoint();
    }

    //Select 
    handleSelectGroup = (selectedGroup) => {

        this.setState({ selectedGroup: selectedGroup, dom_id: selectedGroup.value });

        console.log('selectedGroup', selectedGroup);
    }

    async rewardPoint() {
        let result = await HttpClient.requestData("reward-point", "GET");
        if (result.status && result.data) {
            console.log("resultt", result.data);
            this.setState({ rewardPoint: result.data });
        }
    }

    async handleSubmit() {
        let data = this.state.rewardPoint;
        let dataStatus = await HttpClient.requestData("reward-point", "PUT", data);
        console.log("dataStatus", dataStatus);
        if (dataStatus && dataStatus.status) {
            this.setState({ alert: true, name: "", selectedGroup: null, });
            setTimeout(async () => {
                this.setState({ alert: false });
            }, 5000);
        } else {
            this.setState({ error: true, message: 'Sorry Try Again' });
            setTimeout(async () => {
                this.setState({ error: false });
            }, 5000);
        }
    }

    render() {
        const { selectedGroup } = this.state;
        const optionGroup = [
            {
                options: this.state.domain
            },
        ];

        return (
            <React.Fragment>
                <div className="content">
                    <div className="container-fluid">
                        <div className="page-title-box">
                            <Row className="align-items-center">
                                <Col sm="12">
                                    <h4 className="page-title">Reward Management</h4>
                                    <ol className="breadcrumb">
                                        <li className="breadcrumb-item">
                                            <Link to="/home">
                                                <i className="mdi mdi-home-outline"></i>
                                            </Link>
                                        </li>
                                        <li className="breadcrumb-item">
                                            <Link to="#">Reward Management</Link>
                                        </li>
                                        {/* <li className="breadcrumb-item active">reward management</li> */}
                                    </ol>
                                </Col>
                            </Row>
                        </div>

                        <Row>
                            <Col>
                                <Card>
                                    <CardBody>
                                        <h4 className="mt-0 header-title">Reward Management</h4>
                                        {this.state.alert ? (
                                            <Alert
                                                color="success"
                                                isOpen={this.state.alert}
                                                toggle={() => this.setState({ alert: false })}
                                            >
                                                <i className="mdi mdi-check-all mr-2"></i>
                                                Success !!! Damain Added Successfully
                                            </Alert>
                                        ) : null}
                                        {this.state.error ? (
                                            <Alert
                                                color="danger"
                                                isOpen={this.state.error}
                                                toggle={() => this.setState({ error: false })}
                                            >
                                                <i className="mdi mdi-block-helper mr-2"></i> {this.state.message}
                                            </Alert>
                                        ) : null}
                                        <AvForm>
                                            {
                                                Object.keys(this.state.rewardPoint).map((key) => {
                                                    let item = this.state.rewardPoint[key];
                                                    if (key === "id" || key === "updated_on" || key === "created_on") {
                                                        return null;
                                                    }
                                                    return (
                                                        <AvField
                                                            name={key}
                                                            label={key + " : "}
                                                            placeholder={key}
                                                            type="text"
                                                            errorMessage={"Enter " + key}
                                                            value={item}
                                                            validate={{ required: { value: true } }}
                                                            onChange={val => {
                                                                this.state.rewardPoint[key] = val.target.value;
                                                                this.setState({});
                                                            }}
                                                        />
                                                    )
                                                })
                                            }


                                            <div className="form-group  m-b-0">
                                                <Button
                                                    color="primary"
                                                    onClick={() => {
                                                        this.handleSubmit();
                                                        // onClick={this.add_bClass}
                                                    }}
                                                >
                                                    Submit
                                                </Button>{" "}
                                                &nbsp;
                                                <Button type="reset">cancel</Button>
                                            </div>
                                        </AvForm>
                                    </CardBody>
                                </Card>
                            </Col>
                        </Row>
                    </div>
                </div>
            </React.Fragment>
        );
    }
}

export default connect(null, { activateAuthLayout })(RewardPoint);
