import { Link } from "react-router-dom";
import HttpClient from "../../../utils/HttpClient";




class FormElements extends Component {
  constructor(props) {
    super(props);
    this.state = {
      alert: false,
      error: false,
      message: '',
      selectedFiles: [],
      cartype:"",
      priceperkm:"",
      minprice:"",
      mindistance:"",
      ownerper:"",
      images: [],
      images_new: "",
    };
    this.add_bClass = this.add_bClass.bind(this);
    this.add_vClass = this.add_vClass.bind(this);
  }
  add_bClass = () => {
    document
      .getElementById("bootstrap_validation")
      .classList.add("was-validated");
  };
  add_vClass = () => {
    document.getElementById("b_validation").classList.add("was-validated");
  };
  componentDidMount() {
    this.props.activateAuthLayout();
    // this.FetchDomain();
  }

  //Select 
  



  async handleSubmit() {
let data =new FormData();
  
data.append("cartype",this.state.cartype);
data.append("priceperkm",this.state.priceperkm);
data.append("minprice",this.state.minprice);
data.append("mindistance",this.state.mindistance);
data.append("ownerper",this.state.ownerper);
data.append("image",this.state.images);


    // const data = {
    //   cartype: this.state.cartype,
    //   priceperkm: this.state.priceperkm,
    //   minprice: this.state.minprice,
    //   mindistance: this.state.mindistance,
    //   ownerper:this.state.ownerper
    // }

    console.log("data", data);
    if (this.state.cartype != "" && this.state.priceperkm != '' && this.state.minprice != "" && this.state.mindistance != '' && this.state.ownerper != '') {
      let dataStatus = await HttpClient.fileUplodeDynamic("addcartype", "POST",data);
      console.log("dataStatus", dataStatus);
      if (dataStatus.status) {
        this.setState({
          alert: true, name: "",  cartype:"",
          priceperkm:"",
          minprice:"",
          mindistance:"",
          ownerper:"",
          selectedFiles: [],
        });
        setTimeout(async () => {
          this.setState({ alert: false });
        }, 5000);
      } else {
        this.setState({ error: true, message: 'Sorry Try Again' });
        setTimeout(async () => {
          this.setState({ error: false });
        }, 5000);
      }
    } else {
      this.setState({ error: true, message: 'Please Fill Up All Details' });
      setTimeout(async () => {
        this.setState({ error: false });
      }, 5000);
    }
  }

//   handleAcceptedFiles = async (files) => {
//     this.state.selectedFiles.pop(0);

//     let data = { file: files[0] };
//     let updateData = await HttpClient.fileUplode(
//       "admin/image",
//       "POST",
//       files[0]
//     );
//     // console.log('updateData',updateData);
//     if (updateData && updateData.status) {

//       this.setState({ fetch_img: false })
//       let file = {
//         preview: URL.createObjectURL(files[0]),
//         formattedSize: this.formatBytes(files[0].size)
//       };
//       this.state.selectedFiles.push(file);
//       this.setState({ image: updateData.url });
//     }
//     else {
//       this.setState({ error: true });
//       setTimeout(async () => {
//         this.setState({ error: false });
//       }, 5000);
//     }
//   };
  handleAcceptedFiles = files => {
    let data = [];
    let file = {
      preview: URL.createObjectURL(files[0]),
      formattedSize: this.formatBytes(files[0].size)
      // image:files,
    };
    let file2 = {
      image: files
    };

    let file3 = files;

    // files.map(file => Object.assign(file, {
    //     preview: URL.createObjectURL(file),
    //     formattedSize: this.formatBytes(file.size)
    // }));
    // console.log("data", file);
    this.state.selectedFiles.push(file);
    this.state.images.push(files[0]);
    console.log("data", this.state.images[0].path);
    this.setState({ images_new: file3 });
    // this.setState({ selectedFiles: data });
  };

  /**
   * Formats the size
   */
  formatBytes = (bytes, decimals = 2) => {
    if (bytes === 0) return "0 Bytes";
    const k = 1024;
    const dm = decimals < 0 ? 0 : decimals;
    const sizes = ["Bytes", "KB", "MB", "GB", "TB", "PB", "EB", "ZB", "YB"];

    const i = Math.floor(Math.log(bytes) / Math.log(k));
    return parseFloat((bytes / Math.pow(k, i)).toFixed(dm)) + " " + sizes[i];
  };

  render() {
    return (
      <React.Fragment>
        <div className="content">
          <div className="container-fluid">
            <div className="page-title-box">
              <Row className="align-items-center">
                <Col sm="6">
                  <h4 className="page-title">Add Car Type</h4>
                  <ol className="breadcrumb">
                    <li className="breadcrumb-item">
                      <Link to="/home">
                        <i className="mdi mdi-home-outline"></i>
                      </Link>
                    </li>
                    <li className="breadcrumb-item">
                      <Link to="#">Car Type</Link>
                    </li>
                    <li className="breadcrumb-item active">Add Car Type</li>
                  </ol>
                </Col>
                <Col sm="6">
                  <div className="float-right d-none d-md-block">
                    {/* <Settingmenu /> */}
                  </div>
                </Col>
              </Row>
            </div>

            <Row>
              <Col>
                <Card>
                  <CardBody>
                    <h4 className="mt-0 header-title">Add Car Type</h4>
                    {this.state.alert ? (
                      <Alert
                        color="success"
                        isOpen={this.state.alert}
                        toggle={() => this.setState({ alert: false })}
                      >
                        <i className="mdi mdi-check-all mr-2"></i> Success !!!
                        Car Type Added Successfully
                      </Alert>
                    ) : null}
                    {this.state.error ? (
                      <Alert
                        color="danger"
                        isOpen={this.state.error}
                        toggle={() => this.setState({ error: false })}
                      >
                        <i className="mdi mdi-block-helper mr-2"></i> {this.state.message}
                      </Alert>
                    ) : null}

                    {/* <div className="form-group">
                      <label className="control-label">Select Domain</label>

                      <Select
                        value={selectedGroup}
                        onChange={this.handleSelectGroup}
                        options={optionGroup}
                      />

                    </div> */}
                    <AvForm>
                      <AvField
                        name="cartype"
                        label="Car Type : "
                        placeholder="Car Type"
                        type="text"
                        errorMessage="Enter Car Type"
                        value={this.state.cartype}
                        validate={{ required: { value: true } }}
                        onChange={val =>
                          this.setState({
                            cartype: val.target.value
                          })
                        }
                      />
                      <AvField
                        name="priceperkm"
                        label="Price Per Km : "
                        placeholder="Price Per Km"
                        type="number"
                        errorMessage="Enter Price Per Km"
                        value={this.state.priceperkm}
                        validate={{ required: { value: true } }}
                        onChange={val =>
                          this.setState({
                            priceperkm: val.target.value
                          })
                        }
                      />

                      <AvField
                        name="minprice"
                        label="Minimum Price : "
                        placeholder="Minimum Price"
                        type="number"
                        errorMessage="Enter Minimum Price"
                        value={this.state.minprice}
                        validate={{ required: { value: true } }}
                        onChange={val =>
                          this.setState({
                            minprice: val.target.value
                          })
                        }
                      />

                        <AvField
                        name="mindistance"
                        label="Minimum Distance:"
                        placeholder="Minimum Distance"
                        type="number"
                        errorMessage="Enter Minimum Distance"
                        value={this.state.mindistance}
                        validate={{ required: { value: true } }}
                        onChange={val =>
                          this.setState({
                            mindistance: val.target.value
                          })
                        }
                      />

                       <AvField
                        name="ownerper"
                        label="Owner Percentage:"
                        placeholder="Owner Percentage"
                        type="number"
                        errorMessage="Enter Owner Per"
                        value={this.state.ownerper}
                        validate={{ required: { value: true } }}
                        onChange={val =>
                          this.setState({
                            ownerper: val.target.value
                          })
                        }
                      />

                      <div className="form-group">
                        <label className="control-label">Select Images</label>

                        <Dropzone
                          onDrop={acceptedFiles =>
                            this.handleAcceptedFiles(acceptedFiles)
                          }
                        >
                          {({ getRootProps, getInputProps }) => (
                            <div className="dropzone">
                              <div
                                className="dz-message needsclick"
                                {...getRootProps()}
                              >
                                <input {...getInputProps()} />
                                <i className="h1 text-muted dripicons-cloud-upload"></i>
                                <h3>Drop files here or click to upload.</h3>
                              </div>
                            </div>
                          )}
                        </Dropzone>

                        {this.state.selectedFiles.map((f, i) => {
                          return (
                            <Card
                              className="mt-1 mb-0 shadow-none border dz-processing dz-image-preview dz-success dz-complete"
                              key={i + "-file"}
                            >
                              <div className="p-2">
                                <Row className="align-items-center">
                                  <Col className="col-auto">
                                    <img
                                      data-dz-thumbnail=""
                                      height="80"
                                      className="avatar-sm rounded bg-light"
                                      alt={f.name}
                                      src={f.preview}
                                    />
                                  </Col>
                                  <Col className="pl-0">
                                    <Link
                                      to="#"
                                      className="text-muted font-weight-bold"
                                    >
                                      {f.name}
                                    </Link>
                                    <p className="mb-0">
                                      <strong>{f.formattedSize}</strong>
                                    </p>
                                  </Col>
                                </Row>
                              </div>
                            </Card>
                          );
                        })}


                      </div>

                      <div className="form-group  m-b-0">
                        <Button
                          color="primary"
                          onClick={() => {
                            this.handleSubmit();
                            // onClick={this.add_bClass}
                          }}
                        >
                          Submit
                        </Button>{" "}
                        &nbsp;
                        <Button type="reset">cancel</Button>
                      </div>
                    </AvForm>
                  </CardBody>
                </Card>
              </Col>
            </Row>
          </div>
        </div>
      </React.Fragment>
    );
  }
}

export default connect(null, { activateAuthLayout })(FormElements);
