

async function requestData(url, method, params = null) {
    let bash_url = 'https://devcab.herokuapp.com/api/admin/';
    // let bash_url = "https://dashboard.dialoguein.com/api/v1/";
    let apiUrl = bash_url + url;
    console.log('Url ' + method, apiUrl);
    const myHeaders = new Headers();
    myHeaders.append('Content-Type', 'application/json');
    myHeaders.append("Access-Control-Allow-Origin", "https://devcab.herokuapp.com");
    // myHeaders.append("Access-Control-Allow-Origin", "https://dashboard.dialoguein.com");
    const options = {
        method: method,
        headers: {
            'Accept': 'application/json',
            'Content-Type': 'application/json'
        }
    };
    if (method === 'DELETE') {
        // options['body'] = none;
    } else if (method !== 'GET' ) {
        options['body'] = JSON.stringify(params);
    }
    
    console.log('options', options);
    return await fetch(apiUrl, options)
        .then(res => res.json())
        .then(result => {
            console.log("result", result);
            return result;
        },
        (error) => {
            // this.setState({ error });
        }
    )
}

