async function fileUplodeDynamic(
    url,
    method,
    file,
   // object_get = {},
    tokenCustom = null
  ) {
    let bash_url = "https://devcab.herokuapp.com/api/admin/";
    let apiUrl = bash_url + url;
    // let data = new FormData();
    // data.append("image", file);
    // Object.keys(object_get).forEach(function (key) {
    //   let ddd = object_get[key];
    //   data.append(key, ddd);
    // });
  
    let headers = {
      // 'Accept': 'application/json',
      "Content-Type": "multipart/form-data",
      "Access-Control-Allow-Origin": "https://devcab.herokuapp.com",
      // 'Authorization': 'Bearer ' + login_status,
    };
  
    // Display the key/value pairs
    for (var pair of file.entries()) {
        console.log(pair[0]+ ', ' + pair[1]);
    }
  
    return await fetch(apiUrl, {
      method: "POST",
      body: file,
      redirect: "follow",
    })
      .then((response) => response.json())
      .then(
        (result) => {
          console.log(result);
          return result;
        },
        (error) => {
          return error;
        }
      );
  }
