import React, { Component } from "react";
import { Row, Col, Card, CardBody } from "reactstrap";
import { Link } from "react-router-dom";
import { activateAuthLayout } from "../../../store/actions";
import { connect } from "react-redux";
import Settingmenu from "../Subpages/Settingmenu";
import HttpClient from "../../../utils/HttpClient";
import moment from "moment";
import MultipleGridImages from 'react-multiple-image-grid';

const queryString = require('query-string');

const search = window.location.search;

const current = moment().format("YYYY-MM-DD");

class Uicards extends Component {
  constructor(props) {
    super(props);
    this.state = {
      search:window.location.search,
      post_id: '',
      // post_id:this.props.match.params.id,
      post_title: "",
      user_data: [],
      media: [],
      square_data:[],
      images : [],
      media_status:false
    };
  }
  
  componentDidMount() {
    let params = new URLSearchParams(this.state.search);
    let id = params.get('id');
    this.setState({post_id:id});
    this.props.activateAuthLayout();
    setTimeout(async () => {
      this.FetchSingle();
    }, 100);
    
    // console.log('id',this.props);
    // const parsed = queryString.parse(this.props.location.search);


  }

  async FetchSingle() {
    let result = await HttpClient.requestData(
      "dialogically/show/" + this.state.post_id,
      "GET"
    );
    console.log('result',result);
    if (result && result.status) {
      this.setState({
        post_title: result.data[0].content,
        user_data: result.data[0].user_data,
        media: result.data[0].media,
        created_on:result.data[0].created_on,
        square_data:result.data[0].square_data[0],
      });


      if (result.data[0].media.length>0) {
        for (let i = 0; i < result.data[0].media.length; i++) {
            console.log(result.data[0].media[i].url,'this.state.media[i].url');
            this.state.images.push(result.data[0].media[i].url);
        }
        this.setState({media_status:true})
      }
    }
  }

  render() {
    return (
      <React.Fragment>
        <div className="content">
          <div className="container-fluid">
            <div className="page-title-box">
              <Row className="align-items-center">
                <Col sm="6">
                  <h4 className="page-title">Dialogically</h4>
                  <ol className="breadcrumb">
                    <li className="breadcrumb-item">
                      <Link to="/home">
                        <i className="mdi mdi-home-outline"></i>
                      </Link>
                    </li>
                    <li className="breadcrumb-item">
                      <Link to="#">Dialogically</Link>
                    </li>
                    <li className="breadcrumb-item active">
                      Single Dialogically
                    </li>
                  </ol>
                </Col>
                <Col sm="6">
                  <div className="float-right d-none d-md-block">
                    {/* <Settingmenu /> */}
                  </div>
                </Col>
              </Row>
            </div>

            <Row>
              <Col>
                <h4 className="mb-3">Single Dialogically</h4>
                <div className="card-deck-wrapper">
                  <div className="card-deck">

                    <Card className="mb-12">
                      {/* <img className="card-img-top img-fluid" src={img6} alt="Veltrix" /> */}
                      <br />
                      
                      <Row>
                        {/* {this.state.media.length > 0 ? (
                          <Col lg="4">
                            <div className="card-deck-wrapper">
                              <div className="card-deck">
                                <Card className="mb-4">
                                  <img
                                    className="card-img-top img-fluid"
                                    src={this.state.media[0].url}
                                    alt="Veltrix"
                                  />
                                </Card>
                              </div>
                            </div>
                          </Col>
                        ) : null} */}
                        {this.state.user_data.length>0 ?
                        <Col lg="6">
                        
                          <Card>
                            <CardBody>
                            <h4 className="mt-0 header-title">User Details</h4>
                              <div className="media mb-6">
                                <img
                                  className="d-flex mr-3 rounded-circle"
                                  src={this.state.user_data[0].image}
                                  alt="Veltrix"
                                  height="64"
                                />
                                <div className="media-body">
                                  <h5 className="mt-0 font-16">
                                  {this.state.user_data[0].firstname+" "+this.state.user_data[0].lastname}
                                  </h5>
                                  <p className="text-muted mb-4">{this.state.user_data[0].email}</p>
                                </div>
                              </div>
                            </CardBody>
                          </Card>
                        </Col>
                        :null}

                        {this.state.user_data.length>0 ?
                        <Col lg="6">
                        
                          <Card>
                            <CardBody>
                            <h4 className="mt-0 header-title">Square Details</h4>
                            <div className="media mb-6">
                              
                              <img
                                className="d-flex mr-3 rounded-circle"
                                src={'http://dialoguein.com/assets/images/icon/square.png'}
                                alt="Veltrix"
                                height="64"
                              />
                              <div className="media-body">
                                <h5 className="mt-0 font-16">
                                {this.state.square_data.squ_name}
                                </h5>
                                {/* <p className="text-muted mb-4">{this.state.user_data[0].email}</p> */}
                              </div>
                            </div>
                            </CardBody>
                          </Card>
                        </Col>
                        :null}

                      </Row>
                      <br />
                      <CardBody>
                        <h4 className="card-title font-16 mt-0">Content</h4>
                        <p className="card-text">{this.state.post_title}</p>

                        {this.state.media_status? 
                        <MultipleGridImages images={this.state.images}/>
                        :null}

                        <p className="card-text">
                          <small className="text-muted">
                            {moment(this.state.created_on).format('LLL')}
                          </small>
                        </p>
                      </CardBody>
                    </Card>
                  </div>
                </div>
              </Col>
            </Row>
          </div>
        </div>
      </React.Fragment>
    );
  }
}

export default connect(null, { activateAuthLayout })(Uicards);