import React, { Component } from 'react';
import Layout from './components/Layout/';
import { withRouter, Route, Switch, BrowserRouter as Router, Redirect } from 'react-router-dom';
import Pageslogin from './containers/MainContent/Auth/Login';
import Dashboard from './containers/MainContent/dashboard/dashboard';

import routes from './routes';
import './custom.css';
import './App.scss';

//Fake backend
// import fakeBackend from './helpers/fakeBackend';

// Get all Auth methods
// import { isUserAuthenticated } from './helpers/authUtils';

import { reactLocalStorage } from 'reactjs-localstorage';

// Activating fake backend
// fakeBackend();

function withLayout(WrappedComponent) {
  // ...and returns another component...
  return class extends React.Component {
    render() {
      return <Layout>
        <WrappedComponent></WrappedComponent>
      </Layout>
    }
  };
}

class App extends Component {


  constructor(props) {
    super(props);
    this.state = {
      login: false,
      isLoding: true
    };
  }

  async componentDidMount() {
    let login_data = reactLocalStorage.getObject('login_data');
    if (Object.keys(login_data).length > 0) {
      this.setState({
        login: true,
        isLoding: false
      });
    } else {
      this.setState({
        isLoding: false
      });
    }
  }




  render() {
    if (this.state.isLoding) {
      return <div></div>;
    }

    if (!this.state.login) {
      
      return (
        <Pageslogin getdata={true} loginStatus={(val)=> {
          console.log('sdgsd',val);
          this.setState({
            login: val
          });
        }} />
      )
    }  else {
      
    }

    const PrivateRoute = ({ component: Component, ...rest }) => (
      <Route {...rest} render={(props) => (
        this.state.login === true
          ? <Component {...props} />
          : null
      )} />
    )
    console.log(this.state.login);
    return (
      <React.Fragment>
        <Router>
          {
            this.state.login === true ?
              <Switch>
                {
                  routes.map((route, idx) => 
                      <PrivateRoute path={route.path} component={withLayout(route.component)} key={idx} />
                  )
                }
              </Switch>
              :
              <Switch>
                <Route path="/" name="login" component={Pageslogin} />
                <Route path="/login" name="login" component={Pageslogin} />
              </Switch>
          }
        </Router>
      </React.Fragment>
    );
  }
}


export default withRouter(App);


