import React, { Component } from 'react';
import { Row, Col, Card, CardBody, Alert, Button } from 'reactstrap';
import { Link, withRouter } from 'react-router-dom';
import { connect } from 'react-redux';
import { checkLogin, clearErrorLogin, clearError } from '../../../store/actions';
import { AvForm, AvField } from 'availity-reactstrap-validation';
import HttpClient from "../../../utils/HttpClient";
import { reactLocalStorage } from "reactjs-localstorage";
// import logodark from '../../../images/logo-dark.png';

// const logo = "http://3.15.208.163:8000/assets/images/logo.png";
class Pageslogin extends Component {

    constructor(props) {
        super(props);
        this.state = { 
            username: "", 
            password: "", 
            loginError: 'Inavalid Uesrname Or Password', 
            status: null,
            getdata: this.props.getdata
        }
        // this.handleSubmit = this.handleSubmit.bind(this);
    }


    // this.props.checkLogin(values.username, values.password, this.props.history);

    async handleSubmit() {
        const dataSend = {
            email: this.state.username,
            password: this.state.password,
        }
        console.log("dataSend", dataSend);
        let dataStatus = await HttpClient.requestData('adminlogin', 'POST', dataSend);
        console.log('dataStatus', dataStatus);
        if (dataStatus && dataStatus.status === true) {
            this.setState({
                status: true
            });
            reactLocalStorage.setObject('login_data', dataStatus.data);
            reactLocalStorage.set('loginStatus', true);
            reactLocalStorage.setObject('login', dataStatus.data);
            
            if (this.state.getdata) {
                this.props.loginStatus(true);
            } else {
                this.props.history.push('/home');
            }


        } else {
            this.setState({
                status: false
            })
        }
    }
    // this.props.history.push('/dashboard');


    componentDidMount() {
        document.body.classList.add('bg-primary');
        // alert(this.state.getdata);
        this.props.clearErrorLogin();
        this.props.clearError();
    }

    render() {
        return (
            <React.Fragment>
                {/* <div className="home-btn d-none d-sm-block">
                    <Link to="/" className="text-white"><i className="fas fa-home h2"></i></Link>
                </div> */}
                <div className="account-pages my-5 pt-5">
                    <div className="container">
                        <Row className="justify-content-center">
                            <Col md="8" lg="6" xl="5">
                                <Card className="bg-pattern shadow-none">
                                    <CardBody>
                                        <div className="text-center mt-4">
                                            <div className="mb-3">
                                                <h4 className="font-22 text-center">Cab Admin Dashboard</h4>
                                                {/* <Link to="/" className="logo"><img src={logo} height="20" alt="logo" /></Link> */}
                                            </div>
                                        </div>
                                        <div className="p-3">
                                            {/* <h4 className="font-18 text-center">Login</h4> */}
                                            <p className="text-muted text-center mb-4">Sign in to continue to Cab Admin Dashboard</p>

                                            {this.props.user && <Alert color="success">
                                                Your Login is successfull.</Alert>}

                                            {this.state.status === false ? <Alert color="danger">
                                                {this.state.loginError}</Alert> : null}

                                            <AvForm className="form-horizontal">
                                                <AvField name="username" value={this.state.username}
                                                    onChange={(val) => this.setState({
                                                        username: val.target.value
                                                    })}
                                                    placeholder="Email" type="text" validate={{ email: true, required: true }} />
                                                <AvField name="password" value={this.state.password} placeholder="Password" type="password" required
                                                    onChange={(val) => this.setState({
                                                        password: val.target.value
                                                    })}
                                                />

                                                <div className="mt-3">
                                                    <Button className="btn btn-primary btn-block waves-effect waves-light" type="submit" onClick={() => { this.handleSubmit() }}>Login</Button>
                                                </div>

                                                {/* <div className="mt-4 text-center">
                                                    <Link to="/forget-password"> Forgot your password ?</Link>
                                                </div> */}
                                            </AvForm>
                                        </div>
                                    </CardBody>
                                </Card>
                                {/* <div className="mt-5 text-center text-white-50">
                            <p>Don't have an account ? <Link to="/register" className="font-500 text-white"> Signup now </Link> </p>
                            <p>© {new Date().getFullYear()} Veltrix. Crafted with <i className="mdi mdi-heart text-danger"></i> by Themesbrand</p>
                         </div> */}
                            </Col>
                        </Row>
                    </div>
                </div>

            </React.Fragment>
        );
    }
}

const mapStatetoProps = state => {
    const { user, loginError, loading } = state.Login;
    return { user, loginError, loading };
}

export default withRouter(connect(mapStatetoProps, { checkLogin, clearErrorLogin, clearError })(Pageslogin));



